.App {
  background-color: #da4f4f;
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: top;
  align-items: center;
  flex-direction: column;
}

.poketable {
  background-color: rgb(149, 149, 168);
  grid-area: 'list';
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
  border-radius: 50px;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 92%;
  padding: 50px;
  position:relative;
  
}


.search {
  background-color: yellow;
  grid-area: 'search';
  width:100%;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 5px;
}


.search img {
  width: 120px;
}

.paginate {
  display: flex;
  justify-content: center;
  list-style-type: none;
  gap: 20px;
}

.item {
  width: 200px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  border-radius: 40px 0 20px 20px;
  background-image: radial-gradient(circle at 185px 10px, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 22px, rgb(73, 88, 88) 23px);
}

.pokeName {
  color: white;
  text-transform: capitalize;
  font-size: 22px;
  flex: 30%;
  display: flex;
  align-items: center;
  justify-content: center;
}


.pokeIMG {
  flex: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.typeList {
  display: flex;
  flex: 25%;
  justify-content: center;
  align-items: center;
  gap: 8px;
  width: 100%;

}

.pokeID {
  position: absolute;
  right: -5px;
  top: -10px;
  background-color: #6390F0;
  padding: 5px;
  border-radius: 50%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  width: 30px;
  height: 30px;
}


.pg_button{
  background-color: white;
  border-radius: 50px;  
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  user-select: none;
  transition: background-color 250ms;
  -webkit-box-shadow: 2px 3px 12px -3px #000000;
  box-shadow: 2px 3px 12px -3px #000000;
}

.pg_button:hover{
  color: white;
  background-color: #a1bcf5;
}

.paginationItem {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  background-color: white;
  user-select: none;
  transition: background-color 250ms;
  -webkit-box-shadow: 2px 3px 12px -3px #000000;
  box-shadow: 2px 3px 12px -3px #000000;
  
}

.paginationItem:hover{
  background-color: #a1bcf5;
  color: white;
}

.paginationItemActive {
  background-color: #6390F0;
  color: white;
}

.item::after {
  content: '';
  background-color: rgb(241, 241, 241);
  width: 150px;
  height: 150px;
  position: absolute;
  left: var(50% - 150px);
  top: 75px;
  border-radius: 50%;
}

.typeItems {
  padding: 5px;
  border-radius: 10%;
  color: white;
  text-transform: capitalize;
}

.types {
  z-index: 1;
}

